import React from 'react';

import './proton.min.css';
import './app.scss';

import Header from './components/header';
import Portfolio from './components/portfolio';
import About from './components/about';

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loaded: false,
            examples: [],
            skills: []
        }
    }

    load = async () => {
        const data = await fetch('data.json').then(e => e.json()).catch(err => false);
        data && this.setState({ loaded: true, ...data });
    }

    componentDidMount() {
        this.load();
    }

    render() {
        return (
            <div className='app'>
                <Header />
                <div id='portfolio'></div>
                {this.state.loaded && <Portfolio examples={this.state.examples} />}
                <div id='about'></div>
                {this.state.loaded && <About skills={this.state.skills} />}
            </div>
        );
    }
}
