export default function About({ skills }) {
    return (
        <div className='about'>
            <div className='container'>
                <div className='title'>Sobre mi</div>
                <div className='biography'>Desde pequeño me atraía el inmenso y complejo mundo de las computadoras, hasta que un día decidí adentrarme en él. Comencé aprendiendo lo básico de <b>HTML</b> y <b>CSS</b> y haciendo páginas webs que no tenían ningún tipo de sentido, pero me servían de práctica para entender la semántica y el diseño web. Aprender <b>Javascript</b>, además de agregarle interacción a las páginas, sirvió como ruta para llegar a donde estoy ya que me abrió las puertas a <b>Node Js</b> y <b>React Js</b>, frameworks con los que más trabajo a día de hoy. Como todo buen principiante, dominé <b>PHP</b> casi a la perfección. Pero no podía estancarme sólo en la web, debía seguir expandiéndome, así que aprendí <b>Python</b> y <b>Java</b> y algunos de sus respectivos frameworks y librerias.</div>
                <div className='skills'>
                    <div className='skills-level'>
                        {[['high', 'ALTO'], ['medium', 'MEDIO'], ['low', 'BAJO']].map((e, i) => (
                            <div key={'skills_level_' + i}>
                                <div className='subtitle'>Nivel de experiencia <span className={e[0]}>{e[1]}</span></div>
                                {skills[i].map((o, u) => <img src={'skills/' + o + '.jpg'} alt={o + ' logo'} key={'skills_level_' + i + '_' + u} />)}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
}