export default function Portfolio({ examples }) {
    return (
        <div className='portfolio'>
            <div className='title'>Portafolio</div>
            <div className='examples'>
                {examples.map((e, i) => (
                    <div className='example' key={'example_' + i}>
                        <div className='image-container'><img src={'examples/' + e.img} alt={e.name + ' image'} /></div>
                        <span className='name'>{e.name}</span>
                        <span className='info'>{e.info} <a href={e.link} target='__blank'>Ver</a></span>
                    </div>
                ))}
            </div>
        </div>
    );
}